"use client";

import React, { useState, useMemo } from "react";
import { DoughnutChart, InputGroup } from "../CalculatorUtils";
import { formatRupee } from "../../UI/Utility";
import { LineChart } from "lucide-react";

const InvestmentCalculator = () => {
  const [mode, setMode] = useState("sip");

  const [monthlyInvestment, setMonthlyInvestment] = useState("100");
  const [lumpsumAmount, setLumpsumAmount] = useState("10000");
  const [rate, setRate] = useState("12");
  const [years, setYears] = useState("10");

  const R = Number(rate);
  const Y = Number(years);

  const { totalInvested, totalEarnings, calculatedFV } = useMemo(() => {
    if (R <= 0 || Y <= 0) return { totalInvested: 0, totalEarnings: 0, calculatedFV: 0 };

    const annualRate = R / 100;

    if (mode === "sip") {
      const P = Number(monthlyInvestment);
      if (P <= 0) return { totalInvested: 0, totalEarnings: 0, calculatedFV: 0 };

      const monthlyRate = Math.pow(1 + annualRate, 1 / 12) - 1;
      const months = Y * 12;

      let balance = 0;
      for (let i = 0; i < months; i++) {
        balance += P;
        balance *= 1 + monthlyRate;
      }

      const invested = P * months;
      const fv = Math.floor(balance);

      return {
        totalInvested: invested,
        totalEarnings: fv - invested,
        calculatedFV: fv,
      };
    }

    const L = Number(lumpsumAmount);
    const fv = Math.floor(L * Math.pow(1 + annualRate, Y));

    return {
      totalInvested: L,
      totalEarnings: fv - L,
      calculatedFV: fv,
    };
  }, [mode, monthlyInvestment, lumpsumAmount, R, Y]);

  return (
    <section className="space-y-6">

      {/* ===== HEADER (same language as CalculatorSection) ===== */}
      <header className="bg-slate-900 rounded-xl p-8 text-white shadow-2xl relative overflow-hidden">
        <div className="flex justify-between items-center gap-6">
          <div>
            <span className="text-emerald-500 text-[10px] font-black uppercase tracking-[0.3em] flex items-center gap-2 mb-2">
              <LineChart size={14} /> Investment Tool
            </span>
            <h2 className="text-2xl md:text-3xl font-black uppercase">
              {mode === "sip" ? "SIP Calculator" : "Lumpsum Calculator"}
            </h2>
          </div>

          <div className="flex rounded-xl overflow-hidden border border-white/10">
            <button
              onClick={() => setMode("sip")}
              className={`px-5 py-3 text-xs font-black uppercase tracking-widest transition ${
                mode === "sip" ? "bg-emerald-500 text-black" : "text-slate-300 hover:bg-white/10"
              }`}
            >
              SIP
            </button>
            <button
              onClick={() => setMode("lumpsum")}
              className={`px-5 py-3 text-xs font-black uppercase tracking-widest transition ${
                mode === "lumpsum" ? "bg-emerald-500 text-black" : "text-slate-300 hover:bg-white/10"
              }`}
            >
              Lumpsum
            </button>
          </div>
        </div>
      </header>

      {/* ===== MAIN CARD ===== */}
      <div className="bg-white dark:bg-slate-900 border border-slate-100 dark:border-slate-800 rounded-xl shadow-xl p-8">
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-12">
          
          {/* INPUTS */}
          <div className="lg:col-span-2 space-y-8">
            {mode === "sip" ? (
              <InputGroup
                label="Monthly Investment"
                value={monthlyInvestment}
                setter={setMonthlyInvestment}
                min={100}
                max={1000000}
                step={100}
                unit="₹"
                accentColor="text-emerald-500"
              />
            ) : (
              <InputGroup
                label="Lumpsum Investment"
                value={lumpsumAmount}
                setter={setLumpsumAmount}
                min={1000}
                max={10000000}
                step={1000}
                unit="₹"
                accentColor="text-emerald-500"
              />
            )}

            <InputGroup
              label="Expected Annual Return"
              value={rate}
              setter={setRate}
              min={1}
              max={30}
              step={0.1}
              unit="%"
              accentColor="text-emerald-500"
            />

            <InputGroup
              label="Time Period"
              value={years}
              setter={setYears}
              min={1}
              max={50}
              unit="Yrs"
              accentColor="text-emerald-500"
            />
          </div>

          {/* CHART */}
          <div className="lg:col-span-1 flex justify-center">
            <DoughnutChart
              invested={totalInvested}
              returns={totalEarnings}
              investedColor="#10b981"
              returnsColor="#3b82f6"
            />
          </div>
        </div>

        {/* RESULTS */}
        <div className="mt-10 pt-8 border-t border-slate-100 dark:border-slate-800 grid grid-cols-1 md:grid-cols-3 gap-8">
          <div>
            <p className="text-[10px] font-black uppercase tracking-widest text-slate-400">Invested</p>
            <p className="text-2xl font-black">{formatRupee(totalInvested)}</p>
          </div>

          <div>
            <p className="text-[10px] font-black uppercase tracking-widest text-slate-400">Returns</p>
            <p className="text-2xl font-black text-blue-600 dark:text-blue-400">
              {formatRupee(totalEarnings)}
            </p>
          </div>

          <div className="md:text-right">
            <p className="text-[10px] font-black uppercase tracking-widest text-slate-400">Total Value</p>
            <p className="text-3xl font-black text-emerald-600 dark:text-emerald-400">
              {formatRupee(calculatedFV)}
            </p>
          </div>
        </div>
      </div>
    </section>
  );
};

export default InvestmentCalculator;
